body{
    margin: 0;
    padding: 0;
    background-color: #71927e;
}

main{
    display: flex;
    flex-direction: column;
    margin: 0 8vw;
}

h1{
    font-size: 2rem;
}

h2{
    font-size: 1.5rem;
}

h3{
    font-size: 1.2rem;
}

h1, h2, h3{
    margin: 0;
    padding: 0;
}

a {
    margin: 0;
    padding: 0;
}

@media screen and (max-width: 500px) {
    h1{
        font-size: 12px;
    }
    
    h2{
        font-size: 10px;
    }
    
    h3{
        font-size: 8px;
    }
}